
#app .school-list {
  background-color: #fff;
  padding: 0;
  margin-top: 10px;
}
#app .school-list .header {
    height: 50px;
    line-height: 50px;
    font-size: 16px;
    padding-left: 10px;
    color: #7e57c2;
    border-bottom: 1px solid #eee;
    border-top: 1px solid #eee;
}
#app .school-list .school-item {
    border-bottom: 1px solid #eee;
}
#app .school-list .mu-item {
    min-height: 40px;
    padding-top: 15px;
    padding-bottom: 15px;
    font-size: 14px;
}
#app .school-list .mu-item .mu-item-title {
      font-size: 14px;
      color: #666;
}
#app .school-list .mu-item .mu-icon {
      font-size: 18px;
      color: #999;
}
#app .apply-wrap {
  margin-top: 20px;
  text-align: center;
}
#app .apply-wrap .mu-raised-button {
    border-radius: 40px;
    line-height: 44px;
    height: 44px;
    width: 240px;
}



