
#app .appbar {
  height: 50px;
}
#app .appbar .appbar-title {
    text-align: center;
    font-size: 16px;
}
#app .auth-stepper {
  padding: 0 50px;
  background-color: #fff;
  margin-top: 20px;
}
#app .auth-stepper .auth-step-label {
    height: 50px;
}
#app .auth-stepper-content {
  padding: 10px 20px;
  background-color: #fff;
}
#app .auth-stepper-form .input-label {
  -webkit-transform: scale(1);
          transform: scale(1);
  font-size: 12px;
}
#app .auth-stepper-form .input-content {
  font-size: 14px;
  color: #7e57c2;
}
#app .auth-stepper-form .input-hint {
  font-size: 12px;
}
#app .auth-stepper-success {
  text-align: center;
}
#app .auth-stepper-success .success-text {
    line-height: 2;
    font-size: 16px;
    color: #7e57c2;
    font-weight: bold;
}
#app .auth-stepper-success .mu-icon {
    margin-top: 20px;
}
#app .tip {
  text-align: justify;
  font-size: 12px;
  padding-bottom: 10px;
  color: #666;
}
#app .button-wrap {
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
}
#app .button-wrap .mu-raised-button {
    border-radius: 40px;
    line-height: 44px;
    height: 44px;
    width: 240px;
}



